import { createSlice } from '@reduxjs/toolkit';

let formsReducers = {};

export const contactSlice = createSlice({
    name: 'signupForm',
    initialState: {},
    reducers: {
        updateContactForm: (state, action) => {
            return {...state, ...action.payload}
        },
        resetContactForm: (state, action) => {
            return {}
        }
    },
});
export const { updateContactForm, resetContactForm } = contactSlice.actions;
formsReducers['contactFormReducer'] = contactSlice.reducer;

/* exported to root reducer */
export { formsReducers };