import { createSlice } from '@reduxjs/toolkit'

let dataReducers = {};

export const gdSlice = createSlice({
    name: 'gd',
    initialState: false,
    reducers: {
        setGd: (state, action) => action.payload
    },
})

// Action creators are generated for each case reducer function
export const { setGd } = gdSlice.actions;
dataReducers['gdReducer'] = gdSlice.reducer;

/*---------------------------------------------------------------*/

export const userSlice = createSlice({
    name: 'userData',
    initialState: false,
    reducers: {
        setUser: (state, action) => action.payload
    },
})

// Action creators are generated for each case reducer function
export const { setUser } = userSlice.actions;
dataReducers['userReducer']= userSlice.reducer;

/*---------------------------------------------------------------*/

export const metaTagsSlice = createSlice({
    name: 'userData',
    initialState: false,
    reducers: {
        setMetaTags: (state, action) => action.payload
    },
})

// Action creators are generated for each case reducer function
export const { setMetaTags } = metaTagsSlice.actions;
dataReducers['metaTagsReducer']= metaTagsSlice.reducer;

export { dataReducers };