import { configureStore } from '@reduxjs/toolkit';

import { stateReducers } from "./slices/state";
import { dataReducers }  from "./slices/data";
import { formsReducers } from "./slices/forms";

const Store = configureStore({
    reducer: {
        //state
        deviceState:stateReducers.deviceReducer,
        loaderState:stateReducers.loaderReducer,
        burgerState:stateReducers.burgerReducer,
        popupsArray:stateReducers.popupsReducer,
        requesting:stateReducers.requestingReducer,

        //forms
        contactForm:formsReducers.contactFormReducer,

        //data
        gd: dataReducers.gdReducer,
        userData: dataReducers.userReducer,
        metaTags: dataReducers.metaTagsReducer
    },
    /* remove warning for non serialized data in store */
    middleware: getDefaultMiddleware =>
        getDefaultMiddleware({
            serializableCheck: false,
        }),
});

export default Store;