import React, { Component } from 'react';
import { generateUniqueId } from 'app/functions';

import './index.scss';

class AnimatedTextArea extends Component {
    constructor(props) {
        super(props);
        this.state = {
            animated: false,
        }
    }

    animatePlaceholder(e) {
        let val = e.target.value;
        this.setState({animated:(val !== '')});
        this.props.onChange(e);
    }

    render() {
        const {
            type,
            name,
            placeholder = '',
            value = '',
            id = generateUniqueId(16),
            showError,
            className,
            errorMessage = 'empty or undefined errorMessage prop',
            tabIndex = 10 } = this.props;

        return (
            <div className = {className + ' animated_textarea_wrapper animated' + (showError ? ' error' : '') + (this.state.animated ? ' animate' : '')}>
                <textarea type      = { type }
                          className = 'textarea'
                          name      = { name }
                          id        = { id }
                          value     = { value }
                          onChange  = { (e) => this.animatePlaceholder(e) }
                          tabIndex  = { tabIndex }
                />
                <label htmlFor = { id } className="placeholder">{ placeholder }</label>
                {showError ? <div className = "error-text">{ errorMessage }</div> : ''}
            </div>
        )
    }
}
export default AnimatedTextArea;
