import React                from 'react';
import { useState }         from 'react';
import { generateUniqueId } from 'app/functions';

import './index.scss';

function AnimatedInput(props) {

    const [animated, setAnimated] = useState(false);

    const animatePlaceholder = (e) => {
        let val = e.target.value;
        setAnimated((val !== ''));
        props.onChange(e);
    }
    const {
        type,
        name,
        placeholder = '',
        value = '',
        id = generateUniqueId(16),
        disable_autocomplete = false,
        showError,
        className,
        errorMessage = 'empty or undefined errorMessage prop',
        tabIndex = 10 } = props;

    let is_animated = animated || value !== '';

    return (
        <div className = {'animated-input-wrapper animated ' + className + (showError ? ' error' : '') + (is_animated ? ' animate' : '')}>
            <input type         = { type }
                   name         = { name }
                   value        = { value }
                   id           = { id }
                   className    = { 'input' }
                   onChange     = { (e) => animatePlaceholder(e) }
                   tabIndex     = { tabIndex }
                   autoComplete = { disable_autocomplete ? 'off' : ''}
            />
            <label htmlFor={ id } className="placeholder">{ placeholder }</label>
            {showError ? <div className = "error-text">{ errorMessage }</div> : ''}
        </div>
    )
}
export default AnimatedInput;