import { createSlice } from '@reduxjs/toolkit';

let stateReducers = {};

export const deviceSlice = createSlice({
    name: 'deviceState',
    initialState: false,
    reducers: {
        setDeviceState: (state, action) => action.payload,
    },
})

// Action creators are generated for each case reducer function
export const { setDeviceState } = deviceSlice.actions;
stateReducers['deviceReducer'] = deviceSlice.reducer;

/*---------------------------------------------------------------*/

export const loaderSlice = createSlice({
    name: 'loaderState',
    initialState: false,
    reducers: {
        setLoader: (state, action) => action.payload
    },
});

// Action creators are generated for each case reducer function
export const { setLoader } = loaderSlice.actions;
stateReducers['loaderReducer'] = loaderSlice.reducer;

/*---------------------------------------------------------------*/

export const popupsSlice = createSlice({
    name: 'popupsArray',
    initialState: [],
    reducers: {
        addPopup: (state, action) => {
            state = state.push(action.payload)
        },
        removePopup: (state) => {
            state = state.pop();
        },
    },
})

// Action creators are generated for each case reducer function
export const { addPopup, removePopup } = popupsSlice.actions;
stateReducers['popupsReducer'] = popupsSlice.reducer;

/*---------------------------------------------------------------*/

export const requestingSlice = createSlice({
    name: 'requestingState',
    initialState: false,
    reducers: {
        requestStarted: (state, action) => true,
        requestEnded: (state, action) => false,
    },
});

// Action creators are generated for each case reducer function
export const { requestStarted, requestEnded } = requestingSlice.actions;
stateReducers['requestingReducer'] = requestingSlice.reducer;

/*---------------------------------------------------------------*/

export const burgerSlice = createSlice({
    name: 'requestingState',
    initialState: false,
    reducers: {
        setBurger: (state, action) => action.payload,
    },
});

// Action creators are generated for each case reducer function
export const { setBurger } = burgerSlice.actions;
stateReducers['burgerReducer'] = burgerSlice.reducer;

/*---------------------------------------------------------------*/
export { stateReducers };